


















import Vue from 'vue'
import Component from 'vue-class-component';
import Popup from '@/components/popup/Popup.vue'
import { constants } from 'fs';

@Component({
  name: 'ToastPoup',
  props: {
    title: {
      type: String,
      default: () => '小新提示'
    },
    cancel: {
      type: Function,
      default: () => { }
    },
    btnGroup: {
      type: Array,
      default: () => [{
        name: '再想想',
        color: '#FBD206',
        click: () => {
          console.log('再想想')
        }
      }, {
        name: '确认取消',
        color: '#999999',
        click: () => {
          console.log('确认取消')
        }
      }]
    }

  },
  components: {
    Popup
  }
})
export default class ToastPoup extends Vue {

}



